import React from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar';
import { Container, Nav, Row, Tab,Col } from 'react-bootstrap';

const Skillset = () => {
  return (
    <Container style={{ width: '60%' }}>
      <b id='skills' style={{fontSize:'2rem'}}><box-icon name='visual-studio' type='logo' animation='flashing' size='md'></box-icon> My Skillset</b>
      <p style={{ color: 'gray', fontWeight: 'bold' }}> Here are some of my skills </p>
      <Tab.Container id="myTab" defaultActiveKey="home-tab">
        <Nav variant="tabs" fill>
          <Nav.Item>
            <Nav.Link eventKey="home-tab"><b><i class="uil uil-monitor-heart-rate"></i> Front End</b></Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="backEnd"><b><i class="uil uil-server"></i> Back End</b> </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="devops">DevOps</Nav.Link>
          </Nav.Item>
        </Nav>
        <Tab.Content>
          <Tab.Pane eventKey="home-tab">
            <br />
            <Container>
              <Row>
                <Col className='col-lg-4'><b>React JS</b></Col>
                <Col className='col-lg-8'><ProgressBar striped variant="success" now={80} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4'><b>HTML - CSS</b></Col>
                <Col className='col-lg-8'><ProgressBar striped variant="info" now={70} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4'><b>Bootstrap - Tailwind</b></Col>
                <Col className='col-lg-8'><ProgressBar striped variant="warning" now={90} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4'><b>Angular</b></Col>
                <Col className='col-lg-8'><ProgressBar striped variant="danger" now={66} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4'><b>Javascript</b></Col>
                <Col className='col-lg-8'><ProgressBar striped variant="info" now={80} /><br /></Col>
              </Row>
            </Container>

          </Tab.Pane>

          <Tab.Pane eventKey="backEnd">
          <br />
          <Container>
            <Row>
              <Col className='col-lg-4'><b>Node</b></Col>
              <Col className='col-lg-8'><ProgressBar striped variant="danger" now={66} /><br /></Col>
            </Row>
            <Row>
              <Col className='col-lg-4'><b>Spring</b></Col>
              <Col className='col-lg-8'><ProgressBar striped variant="info" now={70} /><br /></Col>
            </Row>
            <Row>
              <Col className='col-lg-4'><b>Java</b></Col>
              <Col className='col-lg-8'><ProgressBar striped variant="success" now={80} /><br /></Col>
            </Row>
            <Row>
              <Col className='col-lg-4'><b>Python</b></Col>
              <Col className='col-lg-8'><ProgressBar striped variant="warning" now={60} /><br /></Col>
            </Row>
          </Container>

          </Tab.Pane>
          <Tab.Pane eventKey="devops">
          <br />
            <Container>
              <Row>
                <Col className='col-lg-4' ><b><i class="uil uil-cloud-share"></i> AWS </b></Col>
                <Col className='col-lg-8' ><ProgressBar striped variant="danger" now={40} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4' ><b><i class="uil uil-github-alt"></i> Github </b></Col>
                <Col className='col-lg-8' ><ProgressBar striped variant="info" now={80} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4' ><b><i class="uil uil-database"></i> SQL</b></Col>
                <Col className='col-lg-8' ><ProgressBar striped variant="success" now={60} /><br /></Col>
              </Row>
              <Row>
                <Col className='col-lg-4' ><b><i class="uil uil-browser"></i>Heroku </b></Col>
                <Col className='col-lg-8' ><ProgressBar striped variant="warning" now={60} /><br /></Col>
              </Row>
            </Container>
          </Tab.Pane>
        </Tab.Content>
      </Tab.Container>
    </Container>
  );
};

export default Skillset;
